.answer-questionnaire {
  @apply border-t-2 border-background pt-8;
}
.answer-questionnaire__step {
  @apply space-y-10;
}
.answer-questionnaire__step-counter {
  @apply inline-block text-lg text-black font-semibold bg-tertiary;
}
.answer-questionnaire__step-heading {
  @apply space-y-4;
}
.answer-questionnaire__step-heading div {
  @apply text-gray-2 text-lg;
}
.answer-questionnaire__question {
  @apply space-y-4;
}
.answer-questionnaire__question-label {
  @apply text-black text-xl font-semibold relative before:content-[attr(data-answer-idx)] before:w-6 before:h-6 md:before:absolute md:before:-left-4 md:before:-translate-x-full before:inline-flex before:justify-center before:rounded-full before:bg-background before:text-lg before:text-gray-2 before:font-semibold;
}
.answer-questionnaire__question-description {
  @apply text-gray-2;
}
.answer-questionnaire__multiple-option, .answer-questionnaire__single-option {
  @apply text-gray-2 space-y-4;
}
.answer-questionnaire__multiple-option label, .answer-questionnaire__single-option label {
  @apply flex cursor-pointer;
}
.answer-questionnaire__multiple-option label + *, .answer-questionnaire__single-option label + * {
  @apply mt-2 w-full;
}
.answer-questionnaire__multiple-matrix, .answer-questionnaire__single-matrix {
  @apply text-gray-2 text-sm rounded overflow-x-auto;
}
.answer-questionnaire__multiple-matrix table, .answer-questionnaire__single-matrix table {
  @apply w-full;
}
.answer-questionnaire__multiple-matrix th,
.answer-questionnaire__multiple-matrix td, .answer-questionnaire__single-matrix th,
.answer-questionnaire__single-matrix td {
  @apply border border-background px-2 py-2.5;
}
.answer-questionnaire__multiple-matrix thead td,
.answer-questionnaire__multiple-matrix th, .answer-questionnaire__single-matrix thead td,
.answer-questionnaire__single-matrix th {
  @apply bg-background font-normal;
}
.answer-questionnaire__multiple-matrix td:not(:first-child), .answer-questionnaire__single-matrix td:not(:first-child) {
  @apply text-center [&>*]:flex [&>*]:items-center [&>*]:justify-center;
}
.answer-questionnaire__multiple-matrix input[type=text], .answer-questionnaire__single-matrix input[type=text] {
  @apply min-w-[120px] w-full;
}
.answer-questionnaire__sorting {
  @apply flex items-center justify-between relative text-gray-2 pl-8 pr-2 py-2.5 border-2 border-background rounded cursor-pointer before:content-[counter(item)] before:absolute before:left-0 before:top-0 before:bg-background before:w-5 before:h-full before:grid before:place-items-center before:text-gray-2 before:font-bold;
  counter-increment: item;
}
.answer-questionnaire__sorting-container {
  @apply space-y-4;
  counter-reset: item;
}
.answer-questionnaire__sorting input {
  @apply hidden;
}
.answer-questionnaire__sorting svg {
  @apply text-gray fill-current w-5 h-5;
}
.answer-questionnaire__footer {
  @apply flex flex-col gap-10 pt-10 border-t-2 border-background text-gray-2 only:border-t-0;
}
.answer-questionnaire__tos {
  @apply space-y-4 cursor-pointer;
}
.answer-questionnaire__tos div {
  @apply text-sm;
}
.answer-questionnaire__submit {
  @apply flex items-center justify-between;
}
.answer-questionnaire__submit > *:only-child {
  @apply ml-auto;
}